.player-feed {
  display: flex;
  background: #fff;
  width: 100%;
  justify-content: center;
  position: fixed;
  top: 50%;
  overflow-y: scroll;
  height: 100%;
  left: 50%;
  transform: translate(-50%);
  box-shadow: var(--primary) 0px -4px;
  border-radius: 20px;
}

.start-draft {
  position: absolute !important;
  top: 1.25%;
  right: 10px;
  font-size: 1.25rem;
  font-family: "Roboto";
  background: none;
  border-style: none;
}
